<h1 id="用户" tabindex="-1"><a class="header-anchor" href="#用户" aria-hidden="true">#</a> 用户</h1>
<p>用户信息的获取是微信开发中比较常用的一个功能了，以下所有的用户信息的获取与更新，都是<strong>基于微信的 <code>openid</code> 的，并且是已关注当前账号的</strong>，其它情况可能无法正常使用。</p>
<h2 id="获取实例" tabindex="-1"><a class="header-anchor" href="#获取实例" aria-hidden="true">#</a> 获取实例</h2>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token php language-php"><span class="token delimiter important">&lt;?php</span>
<span class="token keyword">use</span> <span class="token package">EasyWeChat<span class="token punctuation">\</span>Foundation<span class="token punctuation">\</span>Application</span><span class="token punctuation">;</span>

<span class="token comment">// ...</span>

<span class="token variable">$app</span> <span class="token operator">=</span> <span class="token keyword">new</span> <span class="token class-name">Application</span><span class="token punctuation">(</span><span class="token variable">$options</span><span class="token punctuation">)</span><span class="token punctuation">;</span>

<span class="token variable">$userService</span> <span class="token operator">=</span> <span class="token variable">$app</span><span class="token operator">-></span><span class="token property">user</span><span class="token punctuation">;</span>
</span></code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br></div></div><h2 id="api-列表" tabindex="-1"><a class="header-anchor" href="#api-列表" aria-hidden="true">#</a> API 列表</h2>
<h3 id="获取用户信息" tabindex="-1"><a class="header-anchor" href="#获取用户信息" aria-hidden="true">#</a> 获取用户信息</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$userService</span><span class="token operator">-></span><span class="token function">get</span><span class="token punctuation">(</span><span class="token variable">$openId</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
<span class="token variable">$userService</span><span class="token operator">-></span><span class="token function">batchGet</span><span class="token punctuation">(</span><span class="token variable">$openIds</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br></div></div><p>获取单个：</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$user</span> <span class="token operator">=</span> <span class="token variable">$userService</span><span class="token operator">-></span><span class="token function">get</span><span class="token punctuation">(</span><span class="token variable">$openId</span><span class="token punctuation">)</span><span class="token punctuation">;</span>

<span class="token keyword">echo</span> <span class="token variable">$user</span><span class="token operator">-></span><span class="token property">nickname</span><span class="token punctuation">;</span> <span class="token comment">// or $user['nickname']</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br></div></div><p>获取多个：</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$users</span> <span class="token operator">=</span> <span class="token variable">$userService</span><span class="token operator">-></span><span class="token function">batchGet</span><span class="token punctuation">(</span><span class="token punctuation">[</span><span class="token variable">$openId1</span><span class="token punctuation">,</span> <span class="token variable">$openId2</span><span class="token punctuation">,</span> <span class="token operator">...</span><span class="token punctuation">]</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><h3 id="获取用户列表" tabindex="-1"><a class="header-anchor" href="#获取用户列表" aria-hidden="true">#</a> 获取用户列表</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$userService</span><span class="token operator">-></span><span class="token function">lists</span><span class="token punctuation">(</span><span class="token variable">$nextOpenId</span> <span class="token operator">=</span> <span class="token constant">null</span><span class="token punctuation">)</span><span class="token punctuation">;</span>  <span class="token comment">// $nextOpenId 可选</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>example:</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$users</span> <span class="token operator">=</span> <span class="token variable">$userService</span><span class="token operator">-></span><span class="token function">lists</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span>

<span class="token comment">// result</span>
<span class="token punctuation">{</span>
 <span class="token string double-quoted-string">"total"</span><span class="token punctuation">:</span> <span class="token number">2</span><span class="token punctuation">,</span>
 <span class="token string double-quoted-string">"count"</span><span class="token punctuation">:</span> <span class="token number">2</span><span class="token punctuation">,</span>
 <span class="token string double-quoted-string">"data"</span><span class="token punctuation">:</span> <span class="token punctuation">{</span>
   <span class="token string double-quoted-string">"openid"</span><span class="token punctuation">:</span> <span class="token punctuation">[</span>
     <span class="token string double-quoted-string">""</span><span class="token punctuation">,</span>
     <span class="token string double-quoted-string">"OPENID1"</span><span class="token punctuation">,</span>
     <span class="token string double-quoted-string">"OPENID2"</span>
   <span class="token punctuation">]</span>
 <span class="token punctuation">}</span><span class="token punctuation">,</span>
 <span class="token string double-quoted-string">"next_openid"</span><span class="token punctuation">:</span> <span class="token string double-quoted-string">"NEXT_OPENID"</span>
<span class="token punctuation">}</span>

<span class="token variable">$users</span><span class="token operator">-></span><span class="token property">total</span><span class="token punctuation">;</span> <span class="token comment">// 2</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br><span class="line-number">14</span><br><span class="line-number">15</span><br><span class="line-number">16</span><br><span class="line-number">17</span><br></div></div><h3 id="修改用户备注" tabindex="-1"><a class="header-anchor" href="#修改用户备注" aria-hidden="true">#</a> 修改用户备注</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$userService</span><span class="token operator">-></span><span class="token function">remark</span><span class="token punctuation">(</span><span class="token variable">$openId</span><span class="token punctuation">,</span> <span class="token variable">$remark</span><span class="token punctuation">)</span><span class="token punctuation">;</span> <span class="token comment">// 成功返回boolean</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>example:</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$userService</span><span class="token operator">-></span><span class="token function">remark</span><span class="token punctuation">(</span><span class="token variable">$openId</span><span class="token punctuation">,</span> <span class="token string double-quoted-string">"僵尸粉"</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><h3 id="获取用户所属用户组id" tabindex="-1"><a class="header-anchor" href="#获取用户所属用户组id" aria-hidden="true">#</a> 获取用户所属用户组ID</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$userService</span><span class="token operator">-></span><span class="token function">group</span><span class="token punctuation">(</span><span class="token variable">$openId</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>example:</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$userGroupId</span> <span class="token operator">=</span> <span class="token variable">$userService</span><span class="token operator">-></span><span class="token function">group</span><span class="token punctuation">(</span><span class="token variable">$openId</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><h2 id="其它" tabindex="-1"><a class="header-anchor" href="#其它" aria-hidden="true">#</a> 其它</h2>
